import { BrowserClient, defaultStackParser, getDefaultIntegrations, makeFetchTransport, Scope } from "@sentry/browser";
import { getUUID } from "../../id/shared/uuid";
import { info } from "../../info/background";
import { Prefs } from "../../../adblockpluschrome/lib/prefs";
export const SENTRY_USER_ID = "sentry_user_id";
let scope;
export function reportError(error) {
    if (scope) {
        scope.captureException(error);
    }
}
let lastEvent;
export function getLastEvent() {
    return lastEvent;
}
export async function initialize(dsn, environment, userId, sampleRate) {
    const manifest = browser.runtime.getManifest();
    const integrations = getDefaultIntegrations({}).filter((defaultIntegration) => {
        return ![
            "BrowserApiErrors",
            "TryCatch",
            "Breadcrumbs",
            "GlobalHandlers"
        ].includes(defaultIntegration.name);
    });
    const client = new BrowserClient({
        dsn,
        environment,
        release: info.addonVersion,
        transport: makeFetchTransport,
        stackParser: defaultStackParser,
        initialScope: {
            tags: {
                manifestVersion: manifest.manifest_version
            }
        },
        integrations,
        sampleRate: sampleRate !== null && sampleRate !== void 0 ? sampleRate : 0.01,
        beforeSend(event) {
            console.warn("Event", event);
            lastEvent = event;
            return event;
        }
    });
    scope = new Scope();
    scope.setClient(client);
    client.init();
    self.addEventListener("error", (event) => {
        const { error } = event;
        if (!(error instanceof Error)) {
            return;
        }
        reportError(error);
    });
    await Prefs.untilLoaded;
    if (!userId) {
        userId = Prefs.get(SENTRY_USER_ID);
        if (!userId) {
            userId = getUUID();
            void Prefs.set(SENTRY_USER_ID, userId);
        }
    }
    scope.setUser({
        id: userId
    });
    const premiumUserId = Prefs.get("premium_user_id");
    scope.setExtra("is_premium_user", Boolean(premiumUserId));
}
export function start() {
    if (webpackDotenvPlugin.SENTRY_DSN != null &&
        webpackDotenvPlugin.SENTRY_ENVIRONMENT != null) {
        void initialize(webpackDotenvPlugin.SENTRY_DSN, webpackDotenvPlugin.SENTRY_ENVIRONMENT);
    }
    else {
        console.warn("Sentry is not initialized. " +
            "Pass `SENTRY_DSN` and `SENTRY_ENVIRONMENT` " +
            "in .env file or env. variables, see README file");
    }
}
