import * as ewe from "@eyeo/webext-ad-filtering-solution";
import { allowlist } from "../../../adblockpluschrome/lib/allowlisting";
import { Prefs } from "../../../adblockpluschrome/lib/prefs";
import * as premium from "../../premium/background";
import { FilterOrigin } from "~/filters/shared";
const trustedSoftonicDomains = [
    "softonic-ar.com",
    "softonic-id.com",
    "softonic-th.com",
    "softonic.cn",
    "softonic.com",
    "softonic.com.br",
    "softonic.com.tr",
    "softonic.jp",
    "softonic.kr",
    "softonic.nl",
    "softonic.pl",
    "softonic.ru",
    "softonic.vn",
    "softoniclabs.com"
];
function getAllowlistingDomain(hostname) {
    if (hostname.includes("softonic")) {
        const domainParts = hostname.split(".");
        while (domainParts.length > 0) {
            const subdomain = domainParts.join(".");
            if (trustedSoftonicDomains.includes(subdomain)) {
                return subdomain;
            }
            domainParts.shift();
        }
    }
    return hostname.replace(/^www\./, "");
}
async function onAllowlisting(domain, options) {
    if (premium.getPremiumState().isActive)
        return;
    await allowlist({
        hostname: getAllowlistingDomain(domain),
        origin: FilterOrigin.web,
        expiresAt: options === null || options === void 0 ? void 0 : options.expiresAt
    });
}
async function removeWebAllowlistingFilters() {
    const allowlistingFilters = (await ewe.filters.getUserFilters()).filter((filter) => filter.type === "allowing");
    const allowlistingFiltersWithMetadata = await Promise.all(allowlistingFilters.map(async (filter) => {
        const metadata = await ewe.filters.getMetadata(filter.text);
        return { filter, metadata };
    }));
    const webAllowlistingFilters = allowlistingFiltersWithMetadata
        .filter(({ metadata }) => metadata && metadata.origin === FilterOrigin.web)
        .map(({ filter }) => filter);
    await ewe.filters.remove(webAllowlistingFilters.map((filter) => filter.text));
}
export async function start() {
    await Prefs.untilLoaded;
    const authorizedKeys = Prefs.get("allowlisting_authorizedKeys");
    void ewe.allowlisting.setAuthorizedKeys(authorizedKeys);
    ewe.allowlisting.setAllowlistingCallback(onAllowlisting);
    if (premium.getPremiumState().isActive) {
        void removeWebAllowlistingFilters();
    }
    premium.emitter.on("activated", async () => {
        void ewe.allowlisting.setAuthorizedKeys([]);
        void removeWebAllowlistingFilters();
    });
    premium.emitter.on("deactivated", () => {
        void ewe.allowlisting.setAuthorizedKeys(authorizedKeys);
    });
}
