import * as ewe from "@eyeo/webext-ad-filtering-solution";
const requestMethods = new Set(["allowing", "header", "request"]);
export function toSerializableBlockableItem(item) {
    const { filter, matchInfo, request } = item;
    const isFrame = !requestMethods.has(matchInfo.method);
    let type;
    if (matchInfo.method === "request" && "type" in request) {
        type = ewe.reporting.contentTypesMap.get(request.type);
    }
    else if (matchInfo.method === "allowing") {
        type = matchInfo.allowingReason;
    }
    else if (filter !== null) {
        if (filter.remove === true) {
            type = "remove";
        }
        else {
            type = matchInfo.method;
        }
    }
    else if ("type" in request) {
        type = ewe.reporting.contentTypesMap.get(request.type);
    }
    if (typeof type !== "string") {
        type = "other";
    }
    return {
        docDomain: matchInfo.docDomain,
        isFrame,
        rewrittenUrl: matchInfo.rewrittenUrl,
        type: type.toUpperCase(),
        url: request.url
    };
}
export function toSerializableFilter(filter) {
    const { csp, enabled, selector, slow, text, type } = filter;
    const serializable = {
        csp,
        selector,
        slow,
        text,
        type
    };
    if (typeof enabled === "boolean") {
        serializable.disabled = !enabled;
    }
    return serializable;
}
export function toSerializableFilterError(filterError) {
    const { option, reason, type } = filterError;
    return {
        option,
        reason,
        type
    };
}
export function toSerializableRecommendation(recommendation) {
    const { languages, title, type, url } = recommendation;
    return {
        languages,
        title,
        type,
        url
    };
}
export function toSerializableSubscription(subscription) {
    const { downloading, downloadStatus, enabled, expires, homepage, lastDownload, lastSuccess, softExpiration, title, updatable, url, version } = subscription;
    return {
        disabled: !enabled,
        downloading,
        downloadStatus,
        expires,
        homepage,
        lastDownload,
        lastSuccess,
        softExpiration,
        title,
        updatable,
        url,
        version
    };
}
